@use "../../GlobalStyles/variables.scss" as *;
@use "../../GlobalStyles/breakPoints.scss" as *;
.navbar {
   width: 100%;
   position: fixed;
   z-index: 99;
   color: #fff;
   padding: 16px;
   display: flex;
   align-items: center;
   justify-content: space-between;

   &.scrolled {
      background: $baseColor;
   }
   & .monile_menu_section {
      position: absolute;
      right: 0;
      right: -100%;
      top: 0;
      height: 100vh;
      width: 50%;
      box-shadow: -4px 0px 8px rgba(0, 0, 0, 0.4);
      background: $baseColor;
      display: flex;
      align-items: center;
      justify-content: center;
      transition: all 0.3s ease-in-out;

      &.open {
         right: 0;
      }

      & .close-btn {
         position: absolute;
         top: 20px;
         left: 20px;
         font-size: 22px;
      }

      & ul {
         list-style: none;
         & li {
            margin-bottom: 22px;
            font-size: 18px;
         }
      }
   }

   &_logo {
      & h1 {
         color: #fff;
         font-size: 44px;

         @include bpMax(m) {
            font-size: 22px;
         }
         & span {
            font-weight: 300;
            color: #fff;
         }
      }
   }
   &_menu {
      list-style: none;
      display: flex;
      gap: 32px;
      font-size: 18px;

      @include bpMax(m) {
         display: none;
      }
   }
   &_mobile_menu {
      display: flex;
      flex-direction: column;
      gap: 6px;

      @include bpMin(m) {
         display: none;
      }
      & div {
         height: 2px;
         width: 30px;
         background: #fff;
         border-radius: 22px;
      }
   }
}
