@use "../../GlobalStyles/variables.scss" as *;
@use "../../GlobalStyles/breakPoints.scss" as *;
.divider {
   position: relative;
   height: 40px;
   display: flex;
   align-items: center;
   justify-content: center;
   color: $baseColor;
   font-size: 32px;

   & .line {
      height: 1px;
      width: 20%;
      background: $baseColor;
      @include bpMax(m) {
         width: 40%;
      }
   }
   & .flower {
      height: 32px;
   }
}
