@use "../../GlobalStyles/variables.scss" as *;
@use "../../GlobalStyles/breakPoints.scss" as *;

.services {
   &_container {
      display: flex;
      flex-direction: column;
      gap: 42px;
      overflow: hidden;

      @include bpMin(l) {
         flex-direction: row;
         justify-content: center;
         //background: blue;
         width: 70%;
         margin: 0 auto;
      }
      & .services_section {
         position: relative;
         background: #fef8f3;
         width: 280px;
         height: 440px;
         border-radius: 22px;

         @include bpMax(xs) {
            width: 90%;
         }
         & .service_image {
            width: 100%;
            height: 200px;

            & img {
               width: 100%;
               height: 100%;
               object-fit: cover;
               border-top-left-radius: 22px;
               border-top-right-radius: 22px;
            }
         }
         & .service_text {
            text-align: center;
            padding: 16px;

            & h3 {
               color: $baseColor;
            }
         }

         .stars {
            position: absolute;

            color: $baseColor;
            &:nth-child(1) {
               left: -10px;
               font-size: 32px;
            }
            &:nth-child(2) {
               right: -22px;
               bottom: 0;
               font-size: 42px;
            }
         }
      }
   }
   .background_wrapper {
      position: relative;
      transform: translateX(100px);
      opacity: 0;
      width: 280px;
      margin: 0 auto;
      @include bpMax(xs) {
         width: 90%;
      }

      & .section_background {
         position: absolute;
         background: #ff9933;
         opacity: 0.5;
         top: 0;
         left: 0;
         height: 100%;
         width: 100%;
         z-index: -1;
         transform: rotate(-5deg);
         border-radius: 22px;

         @include bpMax(xs) {
            width: 90%;
         }
      }
   }
   .section_title {
      overflow: hidden;
   }
}
